.news-post{
  width:100%;
  min-width:300px;
  max-width:900px;
  /*height:200px;*/

  border-style: solid none none none;
  border-width:1px;
  border-color:#aba9a9;
  font-size:18px;
  display:flex;
  justify-content: center;
  align-items: center;
  margin:0 auto;
  padding-bottom: 30px;
  padding-top:15px;
}
.news-flex{
  display: flex;
  flex-direction: row;
}

.newsContent{
  margin-left:10px;
  margin-top:0;

}

.newsTitle{
  font-weight:700;
  margin-left:0px;
  margin-top:0;
}

.newsDescription{
  /*height: 3em;*/
  overflow: hidden;
   text-overflow: ellipsis;
   display: -webkit-box;
   -webkit-box-orient: vertical;
   -webkit-line-clamp: 2; /* number of lines to show */
   line-height:  1;        /* fallback */
   max-height: 2* 1;       /* fallback */
}

.newsContent{
  margin-left:10px;
  /*overflow:hidden;*/
}

.imageContainer{
  height:inherit;
  display: flex;
  justify-content: center;
  align-items: center;
}
.new-image{
  width:200px;
  aspect-ratio:3/2;
  margin:auto;
  object-fit:cover;
  border-radius:5%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.newsDisplay{
  max-width:calc(95% - 300px);
  display:flex;
  flex-direction:column;
  flex: 1 0 auto;
}
.newsContainer{
  display:flex;
  justify-content:center;
  flex-direction:column;
}


@media only screen and (max-width: 900px)  {
  /* For tablets: */
  .newsDisplay{
    width:100%;
    flex: none;
    max-width:100%;
  }
  
  .news-post{
    width:90%;
  }

}
