
* {
  margin: 0;
  padding: 0;
}

html {
    overflow: -moz-scrollbars-vertical; 
    overflow-y: scroll;
}
.mainContent{
  display:flex;
  flex-direction:row;
  margin:0;
}

.navbar{
  flex: 0 0 auto;
  background-color:white;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  display:flex;
  width:300px;
  height:300px;
  /*justify-content:center;*/
  padding-top:100px;
  flex-direction:column;

}
.currentCategory{
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  text-align:center;
  padding-top:60px;
  font-size:30px;
  background-color:white;
  /*border-style: solid none none none;*/
  border-width:1px;
  font-family: "Times New Roman", Times, serif;
  font-weight:500;
  text-decoration-line: underline;
  color:#5e5e5e;

}

.categoryTitle{
  text-align:center;
  font-size:20px;
  padding:10px;
  font-weight:600;
  color:#9d9d9d;
}

.logoContainer{
  display:flex;
  justify-content:center;
  width:100%;
}

.logoImage{
  width:50%;
}

.loadingIconContainer{
  background-color:white;
  width:100%;
  height:200px;
  display:flex;
  justify-content:center;
  align-items:center;
}

.loadingIcon{
  width:100px;
  height:100px;
}
.active-category{
  width:60px;
  position:absolute;
  right:-35px;
  display:flex;
  background-color:transparent;
}
.search-icon{
  float:right;
}

.delete-icon{
  margin-left: 10px;
}

.delete-icon:hover{
  cursor:pointer;
}

.optionTitle{
  width:100%;
  height:100%;
  position:absolute;
  left:1;
}

.optionTitle:hover{
  cursor:pointer;
}

.readMore{
  width:100%;
  text-align:center;
}

@media only screen and (max-width: 600px) {
  /* For phone: */

}

@media only screen and (max-width: 900px)  {
  /* For tablets: */
  .mainContent{
    flex-direction:column;

  }
  .navbar{
    flex: none;
    width:100%;
    position:static;
  }
  .currentCategory{
    display:none;
  }
  .logoImage{
    width:200px;
  }

  .currentCategory{
    padding-top:10px;
  }
  .navbar{
    padding-top:60px;
  }
}
